<template>
    <div class="main-page">
        <template v-if="showHeader">
            <q-card   class="page-section q-py-sm q-px-md q-mb-md nice-shadow-18">
                <div class="container">
                    <div class="row justify-between q-col-gutter-md">
                        <div class="col-12 col-md-auto ">
                            <div class="">
                                <div class="row  items-center q-col-gutter-sm q-px-sm">
                                    <div class="col">
                                        <div class="text-h6 text-primary">Edit Payment Channels</div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </q-card>
        </template>
        <section class="page-section q-mb-md">
            <div class="container">
                <div class="row q-col-gutter-md">
                    <div class="col-md-9 col-12 comp-grid">
                        <q-card   class="q-pa-md nice-shadow-18">
                            <div>
                                <div v-if="!loading">
                                    <ValidationObserver ref="observer" v-slot="{ invalid }" tag="form" @submit.prevent="startRecordUpdate()">
                                        <!--[form-content-start]-->
                                        <div class="row q-col-gutter-md">
                                            <div class="col-12">
                                                <div class="row">
                                                    <div class="col-sm-3 col-12">
                                                        Name *
                                                    </div>
                                                    <div class="col-sm-9 col-12">
                                                        <ValidationProvider :rules="{required:true}" name="Name" v-slot="{ errors, invalid, validated }">
                                                            <q-input outlined dense  ref="ctrlname" v-model="formData.name"  label="Name" type="text" placeholder="Enter Name"   list="name_list"  
                                                            class="" :error="invalid && validated" :error-message="errors[0]">
                                                            </q-input>
                                                            <datalist id="name_list">
                                                            <option v-for="(menu, index) in $menus.nameItems" :key="index" :value="menu.value">{{ menu.label }}</option>
                                                            </datalist>
                                                        </ValidationProvider>
                                                    </div>
                                                </div>
                                            </div>
                                            <div class="col-12">
                                                <div class="row">
                                                    <div class="col-sm-3 col-12">
                                                        Mobile *
                                                    </div>
                                                    <div class="col-sm-9 col-12">
                                                        <ValidationProvider :rules="{required:true}" name="Mobile" v-slot="{ errors, invalid, validated }">
                                                            <q-input outlined dense  ref="ctrlmobile" v-model="formData.mobile"  label="Mobile" type="text" placeholder="Enter Mobile"   list="mobile_list"  
                                                            class="" :error="invalid && validated" :error-message="errors[0]">
                                                            </q-input>
                                                            <datalist id="mobile_list">
                                                            <option v-for="(menu, index) in $menus.nameItems" :key="index" :value="menu.value">{{ menu.label }}</option>
                                                            </datalist>
                                                        </ValidationProvider>
                                                    </div>
                                                </div>
                                            </div>
                                            <div class="col-12">
                                                <div class="row">
                                                    <div class="col-sm-3 col-12">
                                                        Email *
                                                    </div>
                                                    <div class="col-sm-9 col-12">
                                                        <ValidationProvider :rules="{required:true, email:true}" name="Email" v-slot="{ errors, invalid, validated }">
                                                            <q-input outlined dense  ref="ctrlemail" v-model="formData.email"  label="Email" type="email" placeholder="Enter Email"   list="email_list"  
                                                            class="" :error="invalid && validated" :error-message="errors[0]">
                                                            </q-input>
                                                            <datalist id="email_list">
                                                            <option v-for="(menu, index) in $menus.nameItems" :key="index" :value="menu.value">{{ menu.label }}</option>
                                                            </datalist>
                                                        </ValidationProvider>
                                                    </div>
                                                </div>
                                            </div>
                                            <div class="col-12">
                                                <div class="row">
                                                    <div class="col-sm-3 col-12">
                                                        Contact Person *
                                                    </div>
                                                    <div class="col-sm-9 col-12">
                                                        <ValidationProvider :rules="{required:true}" name="Contact Person" v-slot="{ errors, invalid, validated }">
                                                            <q-input outlined dense  ref="ctrlcontact_person" v-model="formData.contact_person"  label="Contact Person" type="text" placeholder="Enter Contact Person"   list="contact_person_list"  
                                                            class="" :error="invalid && validated" :error-message="errors[0]">
                                                            </q-input>
                                                            <datalist id="contact_person_list">
                                                            <option v-for="(menu, index) in $menus.nameItems" :key="index" :value="menu.value">{{ menu.label }}</option>
                                                            </datalist>
                                                        </ValidationProvider>
                                                    </div>
                                                </div>
                                            </div>
                                            <div class="col-12">
                                                <div class="row">
                                                    <div class="col-sm-3 col-12">
                                                        Address *
                                                    </div>
                                                    <div class="col-sm-9 col-12">
                                                        <ValidationProvider :rules="{required:true}" name="Address" v-slot="{ errors, invalid, validated }">
                                                            <q-input outlined dense  ref="ctrladdress" v-model="formData.address"  label="Address" type="text" placeholder="Enter Address"   list="address_list"  
                                                            class="" :error="invalid && validated" :error-message="errors[0]">
                                                            </q-input>
                                                            <datalist id="address_list">
                                                            <option v-for="(menu, index) in $menus.nameItems" :key="index" :value="menu.value">{{ menu.label }}</option>
                                                            </datalist>
                                                        </ValidationProvider>
                                                    </div>
                                                </div>
                                            </div>
                                            <div class="col-12">
                                                <div class="row">
                                                    <div class="col-sm-3 col-12">
                                                        Token *
                                                    </div>
                                                    <div class="col-sm-9 col-12">
                                                        <ValidationProvider :rules="{required:true}" name="Token" v-slot="{ errors, invalid, validated }">
                                                            <q-input outlined dense  ref="ctrltoken" v-model="formData.token"  label="Token" type="text" placeholder="Enter Token"   list="token_list"  
                                                            class="" :error="invalid && validated" :error-message="errors[0]">
                                                            </q-input>
                                                            <datalist id="token_list">
                                                            <option v-for="(menu, index) in $menus.nameItems" :key="index" :value="menu.value">{{ menu.label }}</option>
                                                            </datalist>
                                                        </ValidationProvider>
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                        <!--[form-content-end]-->
                                        <div v-if="showSubmitButton" class="text-center q-my-md">
                                            <q-btn    :rounded="false"  color="primary"  no-caps  unelevated   type="submit" :disabled="invalid" icon="send" :loading="saving">
                                                <q-spinner-oval slot="loading" /> Update
                                            </q-btn>
                                        </div>
                                    </ValidationObserver>
                                    <slot :submit="submit" :saving="saving"></slot>
                                </div>
                                <div v-else class="q-pa-sm text-center">
                                    <q-spinner
                                    :size="40"
                                    color="accent"
                                    indeterminate>
                                    </q-spinner>
                                    Loading...
                                </div>
                            </div>
                        </q-card>
                    </div>
                </div>
            </div>
        </section>
    </div>
</template>
<script>
	import { PageMixin } from "../../mixins/page.js";
	import { EditPageMixin } from "../../mixins/editpage.js";
	import { mapActions } from "vuex";
	export default {
		name: 'editPaymentchannelsPage',
		components: {
		},
		mixins: [PageMixin, EditPageMixin ],
		props: {
			pageName: {
				type: String,
				default: 'payment_channels',
			},
			idName: {
				type: String,
				default: 'id',
			},
			routeName: {
				type: String,
				default: 'payment_channelsedit',
			},
			pagePath: {
				type : String,
				default : 'payment_channels/edit',
			},
			apiPath: {
				type: String,
				default: 'payment_channels/edit',
			},
		},
		data() {
            return {
				formData: {
					name: "", mobile: "", email: "", contact_person: "", address: "", token: "", 
				},
        	}
		},
		computed: {
			pageTitle:{
				get: function () {
					return "Edit Payment Channels"
				}
			}
		},
		meta () {
			return {
				title: this.pageTitle // set browser page title
			}
		},
		methods: {
			...mapActions('payment_channels', ['updateRecord', 'fetchRecord']),
			async startRecordUpdate(){
				const isFormValid = await this.$refs.observer.validate();
				if(isFormValid){
					this.saving = true;
					let id = this.id;
					let url = this.apiUrl;
					let payload = this.normalizedFormData();
					let data = { id, url, payload }
					this.updateRecord(data).then(
						(response) => {
							this.saving = false;
							this.flashMsg(this.msgAfterUpdate);
							this.resetForm();
							this.closeDialogs();// close page dialog that if opened
							this.navigateTo(`/payment_channels`);
						},
						(response) => {
							this.saving = false;
							this.showPageRequestError(response);
						}
					);
				}
			},
			updateFormFields: function(){
				//update form fields value after load from api
				//e.g convert fieldvalue (value,value2,value2) to array 
            },
			resetForm (){
				//reset form fields value
				this.formData = {name: "", mobile: "", email: "", contact_person: "", address: "", token: "", };
				requestAnimationFrame(() => {
					this.$refs.observer.reset();
				});
				//raise event to reset other custom form components
				this.$EventBus.$emit("resetForm");
			},
		},
	};
</script>
<style scoped>
</style>
